.Tarifs {
  min-height: 100vh;
  min-width: 100%;
  background-image: url("https://i.goopics.net/wkz3h3.png");
  background-size: 100%;
  position: relative;
}
.tarifsbox {
  display: flex;
  gap: 50px;
  @include mobile-only {
    flex-direction: column;
  }
}

.tarifsconteneur {
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  @include mobile-only {
    position: unset;
    padding-top : 100px;
    transform: unset;
  }
}

.titretarif {


  & h2 {
    color: #7a6659;
    font-family: "Dancing Script", cursive;
    margin: 0;
  }
  & p {
    color: #7a6659;
    font-family: "Dancing Script", cursive;
    padding-bottom: 20px;
  }
}

.corpsbox {
  width: 500px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  order: 0;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #fdfdfd;
  @include mobile-only {
    width: 100%;
  }
}
.titrecorps {
  color: #7a6659;
  font-family: "Dancing Script", cursive;
  font-size: 32px;
}
.massagesbox {
  order: 1;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
  padding-bottom: 20px;
  padding-top: 20px;
  background-color: #fdfdfd;
  padding-right: 20px;
}
.titremassages {
  color: #7a6659;
  font-size: 32px;
  font-family: "Dancing Script", cursive;
}

.cardmenu {
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

.cardtext {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #7a6659;
  text-align: start;
  padding-left: 20px;
  & h3 {
    color: #7a6659;
    font-family: Helvetica;
  }
  & p {
    color: #7a6659;
    margin: 0;
    font-family: Helvetica;
  }
}

.prix {
  display: flex;
  align-items: start;
  padding-right: 20px;

  & h3 {
    color: #7a6659;
    font-family: Helvetica;
  }
}

.tarifbottom {
  padding-top: 25px;
  padding-bottom: 25px;
  & h3 {
    color: #7a6659;
    font-family: "Dancing Script", cursive;
    margin: 0;
  }
}
